.review-card {
  flex: 0 0 250px; /* Adjust width for each card */
  margin: 0 10px;
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.user-pic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.review-content {
  text-align: left;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.review-main-box
{
  background-color: rgb(250, 250, 250);
}
.review-div-title{
  text-align: center;
}
.user-name {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
}

.review-date {
  font-size: 12px;
  color: #666;
}

.review-rating {
  margin: 5px 0;
  color: #ffc107;
}

.review-description {
  font-size: 14px;
  color: #333;
}

.read-more-btn {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  padding: 0;
  font-size: 14px;
}
