.slider {
    position: relative;
    max-width: 100%;
    height: 400px;
    overflow: hidden;
  }
  
  @media (max-width: 500px) {
    .slider {
      height: 200px;
    }
  }
  

  .slide-image {
    width: 100%;
    height: 100%;
    animation: fade 2s ease;
  }
  
  @keyframes fade {
    from { opacity: 0.4; }
    to { opacity: 1; }
  }
  
  .arrow {
    position: absolute;
    top: 50%;
    font-size: 24px;
    color: white;
    background: rgba(0, 0, 0, 0.5);
    padding: 8px;
    cursor: pointer;
    z-index: 1000;
    transform: translateY(-50%);
  }
  
  .prev {
    left: 10px;
  }
  
  .next {
    right: 10px;
  }
  