.card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    text-align: center;
    padding: 16px;
    transition: transform 0.2s ease;
  }
  
  .card:hover {
    transform: scale(1.05);
  }
  
  .card-image {
    width: 100%;
    height: auto;
    border-radius: 8px 8px 0 0;
  }
  
  .card-name {
    margin: 12px 0 8px;
    font-size: 1.2em;
    color: #333;
  }
  
  .card-description {
    font-size: 0.9em;
    color: #666;
    line-height: 1.4;
  }
  