.review-carousel {
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  max-width: 910px;
  padding: 0px 20px;
  margin: 0 auto;
}

.carousel-container {
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 10px;
  scrollbar-width: none; 
}

.carousel-arrow {
  font-size: 24px;
  color: #333;
  cursor: pointer;
  position: absolute;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  margin: 24px;
  border-radius: 50%;
}

.left {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  left: -20px;
}

.right {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  right: -20px;
}
